<template>
	<view>
		<myp-navbar :fixed="false" title="资讯">
		</myp-navbar>
		<myp-search class="search" :fixIcon="true" iconType="error" placeIconType="error" border="all-error">
		</myp-search>
		<view class="tools">
			<myp-tabs-h class="tools-tabs" :value="current" :isTap="true" :items="tabs" :delay="50"
				activeTextType="error" indicatorType="error" @change="toChange"></myp-tabs-h>
			<myp-button class="tools-btn" icon="rows" @buttonClicked="toTabAll"></myp-button>
		</view>

		<news-list></news-list>
	</view>
</template>

<script>
	import {
		getNewsTabList
	} from '@/api/news.js'
	import newsList from "./components/news/newsList.vue"

	export default {
		components: {
			newsList
		},
		data() {
			return {
				current: 0,
				tabs: ["关注", "推荐", "热榜", "视频", "小说", "北京", "抗疫", "新时代", ""],
			}
		},
		mounted() {
			this.getData()
		},
		methods: {
			getData() {
				getNewsTabList({}).then(res => {
					this.tabs = res.data
					this.tabs.push("")
					console.log(res.data)
				}).catch(e => {
					this.tabs = ["关注", "推荐", "热榜", "视频", "小说", "北京", "抗疫", "新时代", "小视频", "问答", "音乐", "娱乐", "国际",
						"军事", "直播",
						"财经", "影视", "懂车帝", "科技", "美食", "情感", "体育", "健康", "图片", "历史", "法律", "NBA", "漫画", "政法",
						""
					]
				})

			},
			toChange(i) {
				this.current = i
			},
			toSelect(val) {
				this.service = val
				this.allVisible = false
			},
			toTabAll() {
				this.$emit("toTabAll")
			}

		}
	}
</script>


<style lang="scss" scoped>
	.search {
		padding-left: 32rpx;
		padding-right: 32rpx;
	}

	.tools {
		position: relative;

		// &-tabs .uni-scroll-view {
		// 	padding-right: 32px; // 按钮宽度
		// }

		&-btn {
			display: flex;
			align-items: center;
			justify-content: center;
			position: absolute !important;
			right: 0;
			height: 100%;
			width: 32px;
			background-color: white;
		}
	}
</style>
